import React, { Suspense, lazy } from "react";
import LoadingSpinner from "./Components/LoadingSpinner";
// material-ui
import { CssBaseline } from "@mui/material";
import { ThemeProvider } from "@mui/material/styles";
import { theme } from "./utils/theme";
import "./styles.css";

// fonts
import "@fontsource/rubik";
import "@fontsource/luckiest-guy";
// components
const LandingComponent = lazy(() => import("./Components/LandingComponent"));
const HowItWorksComponent = lazy(() =>
  import("./Components/HowItWorksComponent")
);
const JourneyComponent = lazy(() => import("./Components/JourneyComponent"));
const MeetCryptoChefsComponent = lazy(() =>
  import("./Components/MeetCryptoChefsComponent")
);
const TeamComponent = lazy(() => import("./Components/TeamComponent"));
const FaqComponent = lazy(() => import("./Components/FaqComponent"));
const Header = lazy(() => import("./Components/Header"));
const Cookies = lazy(() => import("./Components/CookieConsent"));

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Suspense fallback={<LoadingSpinner />}>
        <Header />
        <LandingComponent />
        <HowItWorksComponent />
        <MeetCryptoChefsComponent />
        <JourneyComponent />
        <TeamComponent />
        <FaqComponent />
        <Cookies />
      </Suspense>
    </ThemeProvider>
  );
}

export default App;
